import "./Contact.scss"
import React, { Component } from "react"
import mapImage from "../../images/Contact/map.webp"
import { Form, Button, FloatingLabel} from "react-bootstrap"

class Contact extends Component {
  state = {
    color: "#E8E8E8"
  }

  update = (event) => {
      var text = event.target.value
      let color = "green"

      if (text.trim() === '') {
          color = '#E8E8E8'
      }
    
      if (text.trim() !== '' && text.length < 5) {
          color = 'red'
      }

      this.setState({color})
  }

  handleSubmit = (event) => {
      event.preventDefault()
  }
  render() {
    const styles = {
      border: `1px solid ${this.state.color}`,
      padding: '0.3em 0.6em',
      outline: 'none'
  }
    return (
      <div className="contact container my-4" id="list-item-7">
        <h2 className="contactTitle text-center fs-1 mb-5">CONTACT</h2>
        <div className="contacContenide row">
          <div className="contactMap col-12 col-sm-6 ">
            <img loading="lazy" className="contactImage mx-auto d-block	img-thumbnail" src={mapImage} alt="imagen"/>
          </div>
          <Form className="contactForm col-12 col-sm-6" onSubmit={this.handleSubmit}>
            <h3 className="contactSubtitle fs-5">Vivamus lacinia odio ut Vivamus lacinia 
            odiout</h3>
            <Form.Group>
              <FloatingLabel controlId="floatingNombre" label="Nombre">
                <Form.Control 
                  className="formTextInput" 
                  type="text" 
                  placeholder="Nombre"
                  /* aria-describedby="nameHelpBlock"  */
                  style={styles} 
                  onChange={this.update} 
                  required
                />
              </FloatingLabel>
              <Form.Text id="nameHelpBlock" muted>
                Your password must be 8-20 characters long, contain letters and numbers, and
                must not contain spaces, special characters, or emoji.
              </Form.Text>
            </Form.Group>
            <Form.Group>
              <FloatingLabel controlId="floatingPhone" label="Phone">
                <Form.Control 
                className="formTelInput" 
                type="tel" 
                placeholder="Phone"
                /* aria-describedby="telHelpBlock"  */ 
                required/>
              </FloatingLabel>
              <Form.Text id="telHelpBlock" muted>
                Your password must be 8-20 characters long, contain letters and numbers, and
                must not contain spaces, special characters, or emoji.
              </Form.Text>
            </Form.Group>
            <Form.Group>
              <FloatingLabel controlId="floatingEmail" label="Email">
                <Form.Control 
                className="formEmailtInput" 
                type="email" 
                placeholder="Email"
                /* aria-describedby="emailHelpBlock"  */ 
                required/>
              </FloatingLabel>
              <Form.Text id="emailHelpBlock" muted>
                Your password must be 8-20 characters long, contain letters and numbers, and
                must not contain spaces, special characters, or emoji.
              </Form.Text>
            </Form.Group>
            <Button  className="formButton w-100 mt-3" type="submit" variant="dark">Send</Button>
          </Form>
        </div>
      </div>
    )
  }
}

export default Contact